.header_outer_container{
    position: sticky;
    top: 0;
    display:flex;
    justify-content: center;
    align-items: center;
    background-color:rgb(20,20,20); 
    height: 12vh;
    color: white;
    transition: all 0.5s;
    transition-timing-function: ease-in;
    z-index: 1;
    }
.nav_black{
    background-color: rgb(20, 20, 20);
    }   
.header_outer_container li{
    margin-right: 5px; 
    margin-left: 10px;
    }  
.nav_black{
    background-color: rgb(20,20,20);
    }
.header_container{
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    align-items: center;

}
.header_left ul{
    width: 140;
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    cursor: pointer;

    }
.header-right ul{
    width: 150;
    display: flex;
    justify-content: space-around;
    list-style: none;
    cursor: pointer;

    }


